.Image-wrap {
  padding: 10px;
  margin: 0;
  position: relative;
  background: #eee;
  display: inline-block;
}

.Image-wrap::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0);
  transition: background-color 0.5s ease;
}



.Image-wrap.hovered::before {
  background: rgba(0, 0, 0, 0.7);
}

.Image {
  text-align: center;
  max-width: 100%;
  float: left;
}

.Image-caption {
  position: absolute;
  left: 0;
  top: calc(50% - 20px);
  text-align: center;
  height: 20px;
  width: 100%;
  color: #efefef;
}

.Image-rights {
  font-size: small;
  position: absolute;
  bottom: 20px;
  color: #efefef;
  text-shadow: 1px 1px 1px #222;
  right: 20px;
}


.Video-wrap {
  padding: 10px;
  margin: 0;
  position: relative;
  background: #eee;
  display: inline-block;
  transition: background-color 0.5s ease;
}

.Video-wrap.hovered {
  background: #222;
}

.Video {
  text-align: center;
  max-width: 100%;
  float: left;
}

.Video-caption {
  font-size: 40px;
  position: absolute;
  left: 0;
  top: calc(50% - 20px);
  text-align: center;
  height: 20px;
  width: 100%;
  color: #efefef;
}

.Video-rights {
  font-size: small;
  position: absolute;
  bottom: 20px;
  color: #efefef;
  text-shadow: 1px 1px 1px #222;
  right: 20px;
}

.List {
  background: #fafafa;
  border: 1px solid #eee;
  padding-top: 10px;
  padding-bottom: 10px;
  transition: all 300ms ease-in;
}

.list {
  list-style: circle;
  padding-left: 1rem;
}

.animatedList-enter {
  opacity: 0.01;
}

.animatedList-enter.animatedList-enter-active {
  opacity: 1;
  transition: all 1000ms ease-in;
}

.animatedList-leave {
  opacity: 1;
}

.animatedList-leave.animatedList-leave-active {
  opacity: 0.01;
  transition: all 1000ms ease-in;
}


